// 岗位管理
import http from '../httpConfig'

const table = "mrbase_sys_job_posts"


// 岗位列表查询接口
export const getPostList = (params = {}) => {
  params['~table~'] = table
  return new Promise((resolve, reject) => {
    http.post('/api/post/list-post/v1', params)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}

